import Alert from '../Alert';
import unit from '../../Helpers/Unit';
import Pagination from '../Pagination';
import TableViewHeader from './TableViewHeader';
import ActivityIndicator from '../ActivityIndicator';
import { each, isObject } from '../../Helpers/Functions';

export default {

    components: {
        Alert,
        Pagination,
        TableViewHeader,
        ActivityIndicator
    },

    props: {

        // (array) An array of table column
        // [{id: 'database_id', name: 'Database id', width: '20%'}]
        columns: {
            type: Array,
            default: () => {
                return [];
            }
        },

        data: {
            type: Array,
            required: true
        },

        // (string) Add table-hover to the table element
        hover: {
            type: Boolean,
            default: true
        },

        loading: Boolean,

        // (integer) The minimum height of the row when loading data
        minHeight: {
            type: Number,
            default: 400
        },

        // (bool) Should show the pagination for this table
        paginate: {
            type: Boolean,
            default: true
        }

    },

    methods: {

        height(min) {
            const elements = [
                // this.$el.querySelector('thead'),
                this.$el.querySelector('tbody')
            ];

            let height = 0;

            each(elements, el => {
                height += el.getBoundingClientRect().height;
            });

            return unit(Math.max(min, height));
        }

    },

    computed: {
        tableColumns() {
            let columns = this.columns;

            if (!columns || !columns.length) {
                columns = Object.keys(this.data[0]);
            }

            return columns.map(column => {
                return isObject(column) ? column : {
                    name: column
                };
            });
        }
    }

};
