import ThumbnailListItem from './ThumbnailListItem';

export default {

    components: {
        ThumbnailListItem
    },

    props: {

        fill: Boolean,

        flex: Boolean,

        noFlex: Boolean,

        grid: Boolean,

        wrap: Boolean,

        images: Array,

        width: {
            type: [String, Number],
            default: 75
        }

    },

    computed: {

        classes() {
            return {
                'thumbnail-list-fill': this.fill,
                'thumbnail-list-flex': this.flex,
                'thumbnail-list-noflex': this.noFlex,
                'thumbnail-list-grid': this.grid,
                'thumbnail-list-wrap': this.wrap
            };
        }

    }

};
