export default {

    name: 'slide-deck-controls',

    props: {

        /**
         * The slide key or index that should show.
         *
         * @type {Number}
         */
        active: {
            type: [String, Number],
            default: 0
        },

        /**
         * An array of slide vnodes
         *
         * @type {Number}
         */
        slides: {
            type: Array,
            required: true
        }

    },

    methods: {

        onClick(event, slide) {
            this.$emit('click', event, slide);
        }

    },

    computed: {

    },

    data() {
        return {};
    }

};
