export default {

    name: 'wizard-progress',

    props: {

        /**
         * The index or key of the active step.
         *
         * @type {String|Number}
         */
        active: {
            type: [String, Number],
            default: 0
        },

        /**
         * The wizard highest available to the user.
         *
         * @type {Array}
         */
        highestStep: {
            type: Number,
            required: true
        },

        /**
         * The wizard steps
         *
         * @type {Array}
         */
        steps: {
            type: Array,
            required: true
        }

    },

    methods: {

        onClick(event, step) {
            if (!event.target.classList.contains('disabled')) {
                this.$emit('click', event, step);
            }
        }

    },

    data() {
        return {
            isActive: false
        };
    }

};
