import NavbarTogglerIcon from './NavbarTogglerIcon';

export default {

    name: 'navbar-toggler',

    components: {
        NavbarTogglerIcon
    },

    props: {

        expanded: Boolean,

        label: {
            type: String,
            default: 'Toggle navigation'
        },

        target: {
            type: String,
            default: '.navbar-collapse'
        }

    },

    methods: {

        onClick(event) {
            this.$emit('click', event);
        }

    }

};
